import {
  AfterViewInit,
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnInit,
  Self,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MAT_DATE_LOCALE } from '@angular/material/core';

// @ts-ignore
import { default as Inputmask } from 'inputmask/dist/inputmask.js';

import { UtilsProvider } from '../../util/utils-provider';
import { TamanhoDatePickerEnum } from '../../enum/datePickerEnums';
import { tamanhoDatePickerType } from '../../types/datePickerTypes';
import { tipoTextoFeedBackType } from '../../types/inputTypes';

@Component({
  selector: 'dsc-datepicker',
  templateUrl: './dsc-datepicker.component.html',
  styleUrls: ['./dsc-datepicker.component.scss'],
  providers: [{ provide: MAT_DATE_LOCALE, useValue: 'pt-BR' }]
})
export class DscDatepickerComponent implements OnInit, OnChanges, AfterViewInit, ControlValueAccessor {
  @ViewChild(MatInput, { static: true })
  dateInput!: MatInput;

  @ViewChild(MatInput, { read: ElementRef, static: true })
  elementRef!: ElementRef;

  @Input()
  dscPlaceHolder?: string;
  dscPlaceHolder_: string = '';

  @Input()
  dscRotulo?: string;
  dscRotulo_: string = '';

  @Input()
  dscInformacaoAdicional?: string;
  dscInformacaoAdicional_?: string;

  @Input()
  dscIconeInformativo?: string;
  dscIconeInformativo_?: string;

  @Input()
  dscTipoTamanhoDatePicker: tamanhoDatePickerType = TamanhoDatePickerEnum.STANDARD;
  dscTipoTamanhoDatePicker_: TamanhoDatePickerEnum = TamanhoDatePickerEnum.STANDARD;

  @Input()
  dscTipoTextoFeedBack?: tipoTextoFeedBackType;
  dscTipoTextoFeedBack_: tipoTextoFeedBackType = 'default';

  @Input()
  dscTextoFeedBack?: string;
  dscTextoFeedBack_: string = '';

  mapaTamanhoDatePicker: Map<TamanhoDatePickerEnum, string> = new Map<TamanhoDatePickerEnum, string>();

  mapaHeader: Map<TamanhoDatePickerEnum, string> = new Map<TamanhoDatePickerEnum, string>();

  classeHeader: string = 'header-standard';

  classFormField = '';

  classComplementar = 'complementar';

  icone_feedBack = '';

  mapaClasseIcone: Map<string, string> = new Map<string, string>();

  classeBox = 'box-input ';

  arrayType = ['danger', 'default'];

  onChange = (_value: any): void => {};

  onTouched = (): void => {};

  disabled = false;

  constructor(@Self() public controlDir: NgControl) {
    controlDir.valueAccessor = this;
    this.inicializarMapaIconeFeedback();
    this.inicializarMapaTamanhoInput();
    this.inicializarMapaHeader();
  }

  inicializarMapaIconeFeedback() {
    this.mapaClasseIcone.set('danger', 'warning');
  }

  ngOnInit(): void {
    this.aplicarClasseMatFormField();
  }

  ngAfterViewInit(): void {
    this._configurarInputMask();
  }

  private _configurarInputMask(): void {
    Inputmask({
      alias: 'datetime',
      inputFormat: 'dd/mm/yyyy',
      placeholder: 'dd/mm/aaaa',
      showMaskOnHover: false,
      clearIncomplete: true
    }).mask(this.elementRef.nativeElement);
  }

  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.dscPlaceHolder, value => {
      this.dscPlaceHolder_ = value;
    });

    UtilsProvider.ifChanged(changes.dscRotulo, value => {
      this.dscRotulo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscInformacaoAdicional, value => {
      this.dscInformacaoAdicional_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIconeInformativo, value => {
      this.dscIconeInformativo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTipoTamanhoDatePicker, value => {
      this.dscTipoTamanhoDatePicker_ = value;
      this.aplicarClasseMatFormField();
    });

    UtilsProvider.ifChanged(changes.dscTextoFeedBack, value => {
      this.dscTextoFeedBack_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTipoTextoFeedBack, value => {
      if (this.arrayType.includes(value)) {
        this.dscTipoTextoFeedBack_ = value;
        this.aplicarClasseFeedBack();
        this.aplicarClasseComplementar();
      } else {
        console.error('Tipo de feedback passado ' + value + ' está inválido');
      }
    });
  }

  aplicarClasseFeedBack() {
    this.classeBox = 'box-input';
    if (this.dscTipoTextoFeedBack_ !== 'default') {
      this.classeBox = this.classeBox + ' ' + this.dscTipoTextoFeedBack_;
      this.icone_feedBack = this.mapaClasseIcone.get(this.dscTipoTextoFeedBack_) || '';
    }
  }

  aplicarClasseComplementar() {
    this.classComplementar = 'complementar';
    if (this.dscTipoTextoFeedBack_ == 'warning') {
      this.classComplementar = this.classComplementar + ' icon-black';
    } else {
      this.classComplementar = this.classComplementar + ' icon-white';
    }
  }

  registerOnChange(fn: (value: any) => void): void {
    this.onChange = fn;
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  writeValue(value: any): void {
    this.dateInput.value = value;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }
  inicializarMapaTamanhoInput() {
    this.mapaTamanhoDatePicker.set(TamanhoDatePickerEnum.LARGE, 'dsc-large');
    this.mapaTamanhoDatePicker.set(TamanhoDatePickerEnum.STANDARD, 'dsc-standard');
    this.mapaTamanhoDatePicker.set(TamanhoDatePickerEnum.SMALL, 'dsc-small');
  }

  inicializarMapaHeader() {
    this.mapaHeader.set(TamanhoDatePickerEnum.LARGE, 'header-large');
    this.mapaHeader.set(TamanhoDatePickerEnum.STANDARD, 'header-standard');
    this.mapaHeader.set(TamanhoDatePickerEnum.SMALL, 'header-small');
  }

  aplicarClasseMatFormField() {
    this.classFormField = '';
    this.classFormField = this.mapaTamanhoDatePicker.get(this.dscTipoTamanhoDatePicker_) || '';
    this.classeHeader = this.mapaHeader.get(this.dscTipoTamanhoDatePicker_) || '';
  }

  isInvalid(): boolean {
    return <boolean>(this.controlDir.invalid && (this.controlDir.touched || this.controlDir.dirty));
  }
}
