import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  Self,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';

// @ts-ignore
import { default as Inputmask } from 'inputmask/dist/inputmask.js';

import { UtilsProvider } from '../../util/utils-provider';
import { tamanhoInputType, tipoTextoFeedBackType } from '../../types/inputTypes';
import { TamanhoInputEnum } from '../../enum/inputEnums';

@Component({
  selector: 'dsc-input',
  templateUrl: './dsc-input.component.html',
  styleUrls: ['./dsc-input.component.scss']
})
export class DscInputComponent implements OnInit, OnChanges, AfterViewInit, ControlValueAccessor {
  @Input()
  dscRotulo?: string;
  dscRotulo_?: string;

  @Input()
  dscInformacaoAdicional?: string;
  dscInformacaoAdicional_?: string;

  @Input()
  dscIconeInformativo?: string;
  dscIconeInformativo_?: string;

  @Input()
  dscIconLeft?: string;
  dscIconLeft_?: string;

  @Input()
  dscPlaceholder!: string;
  dscPlaceholder_!: string;

  @Input()
  dscIsClearButton?: boolean;
  dscIsClearButton_: boolean = false;

  @Input()
  dscTipoTextoFeedBack?: tipoTextoFeedBackType;
  dscTipoTextoFeedBack_: tipoTextoFeedBackType = 'default';

  @Input()
  dscTextoFeedBack?: string;
  dscTextoFeedBack_: string = '';

  @Input()
  dscTextoComplementar?: string;
  dscTextoComplementar_?: string;

  @Input()
  dscIsContadorCaractere?: boolean;
  dscIsContadorCaractere_: boolean = false;

  @Input()
  dscTamanhoMaximo?: number;
  dscTamanhoMaximo_: number = 45;

  @Input()
  dscMask?: any;
  dscMask_: any = '';

  @Input()
  dscIsPassword?: boolean;
  dscIsPassword_: boolean = false;

  @Input()
  dscVoice?: boolean;
  dscVoice_: boolean = false;

  @Input()
  dscTipoTamanhoInput: tamanhoInputType = TamanhoInputEnum.STANDARD;
  dscTipoTamanhoInput_: TamanhoInputEnum = TamanhoInputEnum.STANDARD;

  @Output()
  dscEventEnter: EventEmitter<void> = new EventEmitter<void>();

  @ViewChild('dscInput', { static: true, read: ElementRef })
  input!: ElementRef;

  classeBox: string = 'box-input ';

  arrayType: string[] = ['danger', 'default'];

  iconeFeedBack: string = '';

  mapaClasseIcone: Map<string, string> = new Map<string, string>();

  typeDomInput: string = 'text';

  showMicIcon: boolean = false;

  mapaTamanhoInput: Map<TamanhoInputEnum, string> = new Map<TamanhoInputEnum, string>();

  mapaHeader: Map<TamanhoInputEnum, string> = new Map<TamanhoInputEnum, string>();

  classeHeader: string = 'header-standard';

  classFormField: string = '';

  classComplementar: string = 'complementar';

  stackSuffix: any[] = [];

  disabled: boolean = false;

  onChange = (_value: any): void => {};
  onTouched = (): void => {};

  constructor(@Self() public controlDir: NgControl) {
    controlDir.valueAccessor = this;
    this.inicializarMapaIconeFeedback();
    this.inicializarMapaTamanhoInput();
    this.inicializarMapaHeader();
  }

  ngOnInit(): void {
    this.validarPassword();
    this.configurarInputMask();
    this.aplicarClasseMatFormField();
  }

  ngAfterViewInit(): void {
    this.aplicarMascara();
  }

  ngOnChanges(changes: SimpleChanges) {
    UtilsProvider.ifChanged(changes.dscRotulo, value => {
      this.dscRotulo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscInformacaoAdicional, value => {
      this.dscInformacaoAdicional_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIconeInformativo, value => {
      this.dscIconeInformativo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIconLeft, value => {
      this.dscIconLeft_ = value;
    });

    UtilsProvider.ifChanged(changes.dscPlaceholder, value => {
      this.dscPlaceholder_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIsClearButton, value => {
      this.dscIsClearButton_ = JSON.parse(value);
      this.tratarClasseMatFormFieldClearButton();
    });

    UtilsProvider.ifChanged(changes.dscTipoTextoFeedBack, value => {
      if (this.arrayType.includes(value)) {
        this.dscTipoTextoFeedBack_ = value;
        this.aplicarClasseFeedBack();
        this.aplicarClasseComplementar();
      } else {
        console.error('Tipo de feedback passado ' + value + ' está inválido');
      }
    });

    UtilsProvider.ifChanged(changes.dscTextoFeedBack, value => {
      this.dscTextoFeedBack_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTextoComplementar, value => {
      this.dscTextoComplementar_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIsContadorCaractere, value => {
      this.dscIsContadorCaractere_ = JSON.parse(value);
    });

    UtilsProvider.ifChanged(changes.dscTamanhoMaximo, value => {
      this.dscTamanhoMaximo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscMask, value => {
      this.dscMask_ = value;
      if (this.dscMask_ === '') {
        this.limparMascara();
      } else {
        this.aplicarMascara();
      }
    });

    UtilsProvider.ifChanged(changes.dscIsPassword, value => {
      this.dscIsPassword_ = JSON.parse(value);
    });

    UtilsProvider.ifChanged(changes.dscVoice, value => {
      this.dscVoice_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTipoTamanhoInput, value => {
      this.dscTipoTamanhoInput_ = value;
      this.aplicarClasseMatFormField();
    });
  }

  registerOnChange(fn: (value: any) => void): void {
    this.onChange = fn;
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  writeValue(value: any) {
    this.input.nativeElement.value = value;
  }

  registerOnTouched(fn: () => void) {
    this.onTouched = fn;
  }

  aplicarClasseFeedBack() {
    this.classeBox = 'box-input';
    if (this.dscTipoTextoFeedBack_ !== 'default') {
      this.classeBox = this.classeBox + ' ' + this.dscTipoTextoFeedBack_;
      this.iconeFeedBack = this.mapaClasseIcone.get(this.dscTipoTextoFeedBack_) || '';
    }
  }

  aplicarClasseComplementar() {
    this.classComplementar = 'complementar';
    if (this.dscTipoTextoFeedBack_ == 'warning') {
      this.classComplementar = this.classComplementar + ' icon-black';
    } else {
      this.classComplementar = this.classComplementar + ' icon-white';
    }
  }

  aplicarClasseMatFormField() {
    this.classFormField = '';
    this.classFormField = this.mapaTamanhoInput.get(this.dscTipoTamanhoInput_) || '';
    this.classeHeader = this.mapaHeader.get(this.dscTipoTamanhoInput_) || '';
  }

  limparInput() {
    this.controlDir.reset();
  }

  inicializarMapaIconeFeedback() {
    this.mapaClasseIcone.set('danger', 'warning');
  }

  private aplicarMascara() {
    if (!!this.input && !!this.input.nativeElement) {
      Inputmask(this.dscMask_, {
        autoUnmask: true,
        removeMaskOnSubmit: true,
        showMaskOnHover: false,
        showMaskOnFocus: false,
        placeholder: ''
      }).mask(this.input.nativeElement);
    }
  }

  private limparMascara() {
    if (!!this.input.nativeElement.inputmask) {
      this.input.nativeElement.inputmask.remove();
    }
  }

  private validarPassword() {
    if (this.dscIsPassword_) {
      this.typeDomInput = 'password';
    }
  }

  private configurarInputMask() {
    Inputmask.extendAliases({
      monetario: {
        prefix: 'R$ ',
        groupSeparator: '.',
        alias: 'numeric',
        placeholder: '0',
        autoGroup: true,
        digits: 2,
        digitsOptional: false,
        clearMaskOnLostFocus: true,
        rightAlign: false
      }
    });
  }

  escutarTeclas($event: KeyboardEvent) {
    if (this.dscVoice_ && $event.code === 'F9') {
      this.tratarMicrofone();
    }
  }

  private tratarMicrofone() {
    const { webkitSpeechRecognition } = window as any;
    const voz = new webkitSpeechRecognition();
    const component = this;
    voz.start();
    this.showMic(component);
    voz.addEventListener('result', (results: any) => {
      this.hideMic(component);
      let result = results.results[0][0].transcript;

      component.controlDir.control?.setValue(result);
    });
  }

  private hideMic(component: this) {
    this.aplicarClasseMatFormField();
    component.showMicIcon = false;
    this.retornarSuffixMarcado();
  }

  private showMic(component: any) {
    this.validarSuffix();
    this.mudarClasseParaMic();
    component.showMicIcon = true;
  }

  validarSuffix(): void {
    if (this.dscIsClearButton_) {
      this.stackSuffix.push('clear');
      this.dscIsClearButton_ = false;
    }
    if (this.dscIsPassword_) {
      this.stackSuffix.push('senha');
      this.dscIsPassword_ = false;
    }
  }

  retornarSuffixMarcado() {
    if (this.stackSuffix.includes('clear')) {
      this.dscIsClearButton_ = true;
    }
    if (this.stackSuffix.includes('senha')) {
      this.dscIsPassword_ = true;
    }
    this.stackSuffix = [];
  }

  inicializarMapaTamanhoInput() {
    this.mapaTamanhoInput.set(TamanhoInputEnum.LARGE, 'dsc-large');
    this.mapaTamanhoInput.set(TamanhoInputEnum.STANDARD, 'dsc-standard');
    this.mapaTamanhoInput.set(TamanhoInputEnum.SMALL, 'dsc-small');
  }

  inicializarMapaHeader() {
    this.mapaHeader.set(TamanhoInputEnum.LARGE, 'header-large');
    this.mapaHeader.set(TamanhoInputEnum.STANDARD, 'header-standard');
    this.mapaHeader.set(TamanhoInputEnum.SMALL, 'header-small');
  }

  mudarClasseParaMic() {
    this.classFormField = this.classFormField + ' dsc-icon-suffix-red dsc-body-suffix-white';
  }

  isInvalid(): boolean {
    return <boolean>(this.controlDir.invalid && (this.controlDir.touched || this.controlDir.dirty));
  }

  private tratarClasseMatFormFieldClearButton() {
    const className = ' dsc-clear-button';
    this.classFormField = this.dscIsClearButton_
      ? this.classFormField + className
      : this.classFormField.replace(className, '');
  }
}
