import { Component, Input } from '@angular/core';
import { NgControl } from '@angular/forms';

@Component({
  selector: 'dsc-form-field-error',
  templateUrl: './dsc-form-field-error.component.html',
  styleUrls: ['./dsc-form-field-error.component.scss']
})
export class DscFormFieldErrorComponent {
  @Input() formField!: NgControl;

  @Input() customErrorMessage?: string;
  errorMessage(): string | null {
    return this._mustShowErrorMessage() ? this._getErrorMessage() : null;
  }

  private _mustShowErrorMessage(): boolean {
    return <boolean>(
      (this.formField !== null &&
        this.formField.invalid &&
        (this.formField.touched || this.formField.dirty) &&
        this.formField.errors)
    );
  }

  private _getErrorMessage(): string {
    if (this.formField.errors?.required) return 'Campo obrigatório.';

    if (this.formField.errors?.pattern) return 'Formato inválido';

    if (this.formField.errors?.minlength) {
      const requiredLength = this.formField.errors?.minlength.requiredLength;
      return `O tamanho mínimo deve ser ${requiredLength}.`;
    }

    if (this.formField.errors?.maxlength) {
      const requiredLength = this.formField.errors?.maxlength.requiredLength;
      return `O tamanho máximo deve ser ${requiredLength}.`;
    }

    return this.customErrorMessage ? this.customErrorMessage : '';
  }
}
