import { Component, ElementRef, Input, OnChanges, OnInit, Self, SimpleChanges, ViewChild } from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';

import { tamanhoTextAreaType } from '../../types/textAreaTypes';
import { TamanhoTextAreaEnum } from '../../enum/textAreaEnums';
import { UtilsProvider } from '../../util/utils-provider';

@Component({
  selector: 'dsc-textarea',
  templateUrl: './dsc-textarea.component.html',
  styleUrls: ['./dsc-textarea.component.scss']
})
export class DscTextareaComponent implements OnInit, OnChanges, ControlValueAccessor {
  @Input()
  dscRotulo: string = '';
  dscRotulo_: string = '';

  @Input()
  dscInformacaoAdicional: string = '';
  dscInformacaoAdicional_: string = '';

  @Input()
  dscPlaceholder: string = '';
  dscPlaceholder_: string = '';

  @Input()
  dscTamanhoTextarea: tamanhoTextAreaType = TamanhoTextAreaEnum.STANDARD;
  dscTamanhoTextarea_: TamanhoTextAreaEnum = TamanhoTextAreaEnum.STANDARD;

  @Input()
  dscIconeInformativo?: string;
  dscIconeInformativo_?: string;

  @Input()
  dscTamanhoMaximo?: number;
  dscTamanhoMaximo_: number = 45;

  @Input()
  dscTextoFeedback?: string;
  dscTextoFeedback_?: string;

  @Input()
  dscIsContadorCaractere?: boolean;
  dscIsContadorCaractere_: boolean = false;

  @Input()
  dscTextoComplementar?: string;
  dscTextoComplementar_?: string;

  @ViewChild('dscTextarea', { static: true, read: ElementRef })
  textarea!: ElementRef;

  mapaTamanhoTextarea: Map<TamanhoTextAreaEnum, string> = new Map<TamanhoTextAreaEnum, string>();

  mapaHeader: Map<TamanhoTextAreaEnum, string> = new Map<TamanhoTextAreaEnum, string>();

  classeHeader: string = 'header-standard';

  classeBox: string = 'box-input ';

  classFormField: string = '';

  disabled: boolean = false;
  onChange = (_value: any): void => {};

  onTouched = (): void => {};

  constructor(@Self() public controlDir: NgControl) {
    controlDir.valueAccessor = this;
    this.preencherMapaTamanhoTextarea();
    this.preencherMapaHeader();
  }

  ngOnInit(): void {
    this.tratarClassesDinamicas();
  }

  ngOnChanges(changes: SimpleChanges): void {
    UtilsProvider.ifChanged(changes.dscRotulo, value => {
      this.dscRotulo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscInformacaoAdicional, value => {
      this.dscInformacaoAdicional_ = value;
    });

    UtilsProvider.ifChanged(changes.dscPlaceholder, value => {
      this.dscPlaceholder_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTamanhoMaximo, value => {
      this.dscTamanhoMaximo_ = value ? value : 45;
    });

    UtilsProvider.ifChanged(changes.dscTamanhoTextarea, value => {
      this.dscTamanhoTextarea_ = value;
      this.tratarClassesDinamicas();
    });

    UtilsProvider.ifChanged(changes.dscIconeInformativo, value => {
      this.dscIconeInformativo_ = value;
    });

    UtilsProvider.ifChanged(changes.dscTextoFeedBack, value => {
      this.dscTextoFeedback_ = value;
    });

    UtilsProvider.ifChanged(changes.dscIsContadorCaractere, value => {
      this.dscIsContadorCaractere_ = JSON.parse(value);
    });

    UtilsProvider.ifChanged(changes.dscTextoComplementar, value => {
      this.dscTextoComplementar_ = value;
    });
  }

  registerOnChange(fn: (value: any) => void): void {
    this.onChange = fn;
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  writeValue(value: any) {
    this.textarea.nativeElement.value = value;
  }

  registerOnTouched(fn: () => void) {
    this.onTouched = fn;
  }

  private tratarClassesDinamicas() {
    this.classFormField = '';
    this.classFormField = this.mapaTamanhoTextarea.get(this.dscTamanhoTextarea_) || '';
    this.classeHeader = this.mapaHeader.get(this.dscTamanhoTextarea_) || '';
  }

  private preencherMapaTamanhoTextarea() {
    this.mapaTamanhoTextarea.set(TamanhoTextAreaEnum.LARGE, 'dsc-large');
    this.mapaTamanhoTextarea.set(TamanhoTextAreaEnum.STANDARD, 'dsc-standard');
    this.mapaTamanhoTextarea.set(TamanhoTextAreaEnum.SMALL, 'dsc-small');
  }

  private preencherMapaHeader() {
    this.mapaHeader.set(TamanhoTextAreaEnum.LARGE, 'header-large');
    this.mapaHeader.set(TamanhoTextAreaEnum.STANDARD, 'header-standard');
    this.mapaHeader.set(TamanhoTextAreaEnum.SMALL, 'header-small');
  }

  isInvalid(): boolean {
    return <boolean>(this.controlDir.invalid && (this.controlDir.touched || this.controlDir.dirty));
  }
}
